import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import api from '../assets/gapi';

const styles = theme => ({
  button: {

  }
});

class App extends Component {

  state = {
    link: 'https://reactjs.org',
    user: api.isAuthorized ? api.user : null,
  };

  constructor (props) {
    super(props);
    api.onStatusUpdate = status => this.setState({ user: status ? api.user : null });
  }

  authHandler = () => {
    api.authorize()
      .then(user => this.setState({ user }));
  }

  runHandler = () => {
    api.fetch(this.state.link)
      .then(response => console.log(response));
  }

  changeHandler = name => event => {
    this.setState({ [name]: event.target.value });
  }

  render() {

    const { classes } = this.props,
      { link, user } = this.state;

    return (
      <div className="App">
        <TextField
          label="Link"
          fullWidth
          margin="normal"
          variant="outlined"
          value={link}
          onChange={this.changeHandler('link')}
        />
        <Button color="primary" className={classes.button} onClick={this.runHandler} >
          Fetch
        </Button>
        <Button className={classes.button} onClick={this.authHandler} >
          { user ? `LOG OUT ${user.w3.ig}` : 'LOG IN' }
        </Button>
      </div>
    );
  }

}

export default withStyles(styles)(App);
